import { defineStore } from 'pinia'
// import { stringify } from 'csv-stringify/sync';
import axios from 'axios'

export const useCsvStore = defineStore({ 
    id: 'csvstore',
    state: () => ({
        csvdata: "",
        rawdata: null,
        basePath: "https://testspiel.danielglueck.de/wp-json",
        taskPath: "/wp/v2/erhebungsdaten",
        allposts: "?per_page=100",
        offset: "offset=",
        perpage: "per_page=100"
    }), 
    getters: {

    },
    actions: {

        async fetchCSVDATA(){

            this.csvdata = ""
            this.rawdata = null

            //first loop through a 1000
            for (let i = 0; i < 100000; i+=100) { 
                
                var offsetString = "?" + this.offset + i + "&" + this.perpage
                var requestString = this.basePath + this.taskPath + offsetString
                console.log(requestString)

                this.rawdata = this.rawdata + await axios.get(requestString).then((response) => {
                    console.log(response.data)
    
                    if (response.data == null || response.data == "" || response.data.length == 0) {
                        i = 1000
                        return ""
                    }

                    var lines = ''
                    response.data.forEach(
                        (datalog) => {
                       

                            console.log(datalog.title.rendered)
                            var line = datalog.title.rendered + ", " + datalog.aufgabentitel + ", " +  datalog.zp_begonnen + ", " + datalog.zp_abgeschlossen + "," +
                            datalog.zp_spielbeginn + ", " + datalog.nutzername + "," + datalog.einfach + " <br> "
                            lines = lines + line
                        }
                    )
    
                    console.log(lines)
                    
                    const output = lines
                    const tablenames = "Titel, AufgabenTitel, Beginn, Ende, SpielBeginn, Nutzer, Einfach <br>"
                    const result = tablenames + output
                    this.csvdata = this.csvdata + result
                    
                   
                        
                    return result
                }).catch(
                    error => console.log(error)
                ).finally(
                    () => { 
                        this.loading = false
                    }
                )

            }

        }
    }
 })
  